body {
    margin: 0;
    padding: 0;
    font-family: Arial, sans-serif;
  }
  
  .log {
    display: flex;
    height: auto;
  }
  
  .content {
    display: flex;
    align-items: center;
  }
  .image {
    position: relative;
  }
  .image img {
    width: 110%; 
    height: 650px;
  }
  .logo img {
    position: absolute;
    top: 20px;
    left: 20px;
  }
  
  .container123 {
    margin-top: 80px;
    border-radius: 8px;
    margin-left: 200px;
    
  }
  h2 {
    margin-bottom: 40px;
  }
  p {
    letter-spacing: 8px;
  }
  
  .input-group {
    margin-bottom: 20px;
  
  }
  .input-group label {
    display: block;
    font-weight: bold;
    margin-bottom: 5px;
  }
  .input-group input, .input-group select {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  .btn {
    width: auto;
    padding: 10px 40px;
    border: none;
    background-color: black;
    color: #fff;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 15px;
  }
  
  .btn:hover {
    background-color: #0056b3;
  }
  
  .switch-user {
    margin-bottom: 20px;
  }
  
  .switch-user label {
    display: block;
    margin-bottom: 5px;
  }
  
  .switch-user select {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .options {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 10px;
  }
  
  .options label {
    display: inline-block;
    margin-right: 10px;
  }
  .login-container {
    display: none;
  }
  @media screen and (max-width: 1150px){
    .image img {
        width: 590px; 
    }
  }
