  @media screen and (max-width:1000px){
  
    .log{
      display: none;
    }
    .login-container {
      padding: 30px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      margin-top: 5 0px;
      height: 100vh;
      background-color: #fff;
    }
    .logo-container {
      margin-bottom: 50px;
      text-align: center;
    }
    .truck_logo{
      margin-top:20px ;
      /* margin-left: 50px; */
    }
    .truck_logo img{
      border-radius:50px;
      width: 150px;
      height: 150px;
    }
    .logo {
      width: auto; 
      margin-bottom: 20px;
    }
    .login-form {
      margin-top: -20px;
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 100%;
      max-width: 300px;
    }
    .input-field {
      width: 100%;
      padding: 10px;
      margin-bottom: 30px;
      border: none;
      border-radius: 5px;
      font-size: 16px;
    }
    
    .login-button {
      width: 100%;
      padding: 15px;
      background-color: #333;
      border: none;
      border-radius: 5px;
      font-size: 16px;
      font-weight: bold;
      cursor: pointer;
    }
  .login-form label {
    padding: 10px;
    margin-left: -150px;
  }
  .option {
    margin-top: 20px;
  
     /* justify-content: left; */
     /* align-self: baseline; */
     /* display: flex;   */
  }
  .option label{
    padding-top: 30px;
    margin-left: -35px;
  }
  .option a{
    margin-left: 20px;
    text-decoration: none;
  }
  .login-button{
    margin-top: 20px;
  }
    .login-button:hover {
      background-color: #333;
    }
  }